import React, { Component } from 'react'
import { Div, Title, Text } from '@vkontakte/vkui';

const How_Work = ({ sex }) => (
    <Div>
       <Title weight='heavy' level='3'>Как это работает?</Title>
       <Text>
           {sex == 2 ? 
           <div>
               <p>
               Мы думаем не секрет, что рядом с тобой в самолете будут лететь пассажиры, которые заплатили за один и тот же авиабилет разную стоимость. Или больше или меньше. Не веришь? Спроси при случае у симпатичной брюнетки слева...
               </p>
               <p>
               Это - динамическое ценообразование, при котором цена постоянно меняется и зависит от спроса и предложения, количества дней до вылета, распродаж и акций, цен на топливо и аэропортовых сборов, ограничительных мер и от многого другого.
               </p>
               <p>
               Можно следить за информацией об авиараспродажах и акциях в новостях профильных пабликов, в своей электронной почте и других источниках, но проще получать данные из первоисточников.
               </p>
               <p>
               Что мы имеем в виду?
               </p>
               <p>
               Представьте, что кто-то купил дешевый авиабилет и написал Вам об этом. Это так и работает, только еще надежнее - без участия людей.
               </p>
               <p>
               Когда кто-нибудь находит авиабилет в поисковике и покупает его, данные о стоимости, пунктах вылета и назначения не стираются из системы поиска.
               </p>
               <p>
               Эта информация сравнивается с уровнем стоимости таких же авиабилетов в этот промежуток времени и, если купленный авиабилет дешевле на 30% или больше такого уровня - он попадает в наш гипермаркет "Дешевые авиабилеты".
               </p>
               <p>
               Так как первичный анализ происходит без учета количества промежуточных пересадок, то далее, чтобы избежать ошибок, проводится повторный анализ уже среди "дешевых" авиабилетов.
               </p>
               <p>
               Уровень минимальной стоимости постоянно меняется с учетом новых поступающих данных и пропускаются дешевые авиабилеты в коридоре цен, установленном нами на уровне 15-20% от минимума.
               </p>
               <p>
               Зачем коридор минимальных цен? В одни и те же места пассажиры летают в течение всего месяца и из месяца в месяц. Если зафиксировать самый дешевый авиабилет в начале месяца и не показывать билеты дороже, то больше никто не сможет увидеть дешевые авиабилеты в это место и в этом месяце.
               </p>
               <p>
               Но самолет с пассажирами, купившими "те", самые дешевые авиабилетами уже улетел...
               </p>
               <p>
               Представь, что выгодность "тех" авиабилетов была больше 60%? Это значит, что дешевые авиабилеты с выгодой 30, 40 и 50% не будут показаны, а при существовании 20% коридора цен мы покажем дешевые авиабилеты с выгодой. начиная с 48%.
               </p>
               <p>
               Если речь идет о разделе "Витрина", в котором можно увидеть дешевые авиабилеты во все страны мира из всех стран мира, в которых живут более 0,4% русскоязычного населения, то работает дополнительный аналитический фильтр.
               </p>
               <p>
               На стене сообществ в ВКонтакте можно публиковать не более 50 записей, поэтому для сообществ, показывающих наиболее насыщенные трафики полетов, мы включаем фильтр, отсеивающий дешевые авиабилеты в одно и то же направление в течение 24 часов для того, чтобы максимально разнообразить ассортимент информации.
               </p>
               <p>
               Мы также добавляем прогноз погоды (днем и ночью) для даты приземления, включая возможность осадков, ссылку на карту местности в месте приземления и статью из Википедии о городе назначения.
               </p>
               <p>
               Цена дешевого авиабилета показывается в местной валюте и в рублях, если это вылеты из России и в местной валюте, в долларах и евро, если это широко ориентированные международные вылеты.
               </p>
               <p>
               Авиабилеты разлетаются как "горячие пирожки" в течение недолгого времени. Дешевые авиабилеты нас не ждут - все наоборот!
               </p>
               <p>
               Поэтому в каждом билете мы показываем сразу 4 ссылки для его покупки. Не теряй времени на изменение параметров после показа авиабилетов на одного пассажира. Выбирай сразу - летишь один или вдвоем в эконом-классе или один в бизнес-классе и переходи по нужной ссылке с уже введенными параметрами.
               </p>
               <p>
               У тебя другой вариант? Выбери свободную форму поиска и введи данные сам.
               </p>
               <p>
               Хочешь посмотреть, например, все дешевые авиабилеты в выбранный город на стене сообщества?
               </p>
               <p>
               Используй готовые хэштеги в каждом авиабилете для моментального показа нужных билетов в определенный город или выбранную страну.
               </p>
               <p>
               Хочешь увидеть в режиме реального времени в одной новостной ленте сразу все дешевые авиабилеты гипермаркета?
               </p>
               <p>
               Используй хэштег #авиабилеты. Ты тоже найдешь его в каждом дешевом авиабилете.
               </p>
               <p>
               Хочешь получать все дешевые авиабилеты в ленту новостей? Подпишись на нужное сообщество в разделе "Витрина".
               </p>
               <p>
               Торопишься, листая ленту новостей? Фотография достопримечательности в городе назначения поможет быстро найти нужный билет на стене сообщества или в своей ленте новостей.
               </p>
               <p>
               Хочешь в личные сообщения?
               </p>
               <p>
               Подпишись на выборочные (или все) направления полетов из нужного сообщества в разделе "Витрина". Один раздел - все возможности работы с 700 сообществами гипермаркета "Дешевые авиабилеты".
               </p>
               <p>
               Подписался на определенное направление полетов и интересуют другие?
               </p>
               <p>
               В каждом дешевом авиабилете мы показываем WIKI-меню с картой полетов по всему миру. Выбирай.
               </p>
               <p>
               А еще мы показываем количество пересадок, название авиакомпаний-перевозчиков и дни недели вылета.
               </p>
               <p>
               Помнишь про симпатичную брюнетку слева в салоне самолета? Не спрашивай, сколько она заплатила за авиабилет, если ты купил свой в нашем гипермаркете, не надо.
               </p>
               <p>
               Теперь ты знаешь, как все работает. Загляни в своё будущее сегодня и добро пожаловать на борт
               </p>
           </div>:
           <div>
            <p>Мы думаем, для тебя не секрет, что рядом в самолете будут лететь пассажиры, которые заплатили за один и тот же авиабилет разную цену. Или больше или меньше. Не веришь? Спроси при случае у молодого человека справа... Не хочешь? И правильно! А причину ты узнаешь в конце статьи.</p>

            <p>Это называется - динамическое ценообразование, при котором цена постоянно меняется и зависит от спроса и предложения, количества дней до вылета, распродаж и акций, цен на топливо и аэропортовых сборов, ограничительных мер и еще от многих других причин и причинок.</p>
            
            <p>Можно следить за информацией об авиараспродажах и акциях в новостях профильных пабликов, искать письма от авиакомпаний в электронной почте или положиться на статус в программе лояльности авиаальянса, но проще получать данные из первоисточников.</p>
            
            <p>Что мы имеем в виду?</p>
            
            <p>Представьте, что одна девушка купила дешевый авиабилет и написала об этом всем другим девушкам. Это сложно представить? Но это так и работает, только еще надежнее - без участия людей. Нет, люди покупают авиабилеты, обычно выбирая самые дешевые, но все остальное делают роботы.</p>
            
            <p>Когда кто-нибудь находит авиабилет в поисковике, то данные о его стоимости, пунктах вылета и назначения не стираются из системы поиска.</p>
            
            <p>Эта информация сравнивается с уровнем цены таких же авиабилетов в этот промежуток времени и, если купленный авиабилет дешевле на 30% или больше этого уровня - он попадает в наш гипермаркет "Дешевые авиабилеты".</p>
            
            <p>Мы добавляем информацию о прогнозе погоды (днем и ночью) для даты приземления (смело положи в рюкзачок купальник), включая возможность осадков (и зонтик не на всякий случай), ссылку на карту местности в месте приземления и статью из Википедии о городе назначения.</p>
            
            <p>Цена дешевого авиабилета показывается в местной валюте и в рублях, если это вылеты из России и в местной валюте, в долларах и евро, если это широко ориентированные международные вылеты.</p>
            
            <p>Мы показываем информацию на английском языке в 74 сообществах дешевых авиабилетов для вылетов из США, на казахском - для вылетов из Казахстана, на белорусском - для Беларуси, на иврите - для городов вылета в Израиле и т.д.</p>
            
            <p>Авиабилеты разлетаются как "горячие пирожки" в течение недолгого времени. Дешевые авиабилеты нас не ждут - все наоборот!</p>
            
            <p>Поэтому в каждом билете мы показываем сразу 4 ссылки для его покупки. Не теряй времени на изменение параметров после показа авиабилетов на одного пассажира. Выбирай сразу - летишь один или вдвоем в эконом-классе или один в бизнес-классе и переходи по нужной ссылке с уже введенными параметрами.</p>
            
            <p>У тебя другой вариант? Выбери свободную форму поиска и введи данные сама.</p>
            
            <p>Это похоже на непрекращающуюся ни на минуту постоянную распродажу. Мелькают скидки, покупатели, цены, авиабилеты – все смешалось в круговорот аэропортов, взлетов и посадок.</p>
            
            <p>Смело ориентируйся в потоке информации, заходя в наш гипермаркет «Дешевые авиабилеты». Поброди спокойно, помахивая сумочкой по 700 витринам или создай свою в разделе «Мой выбор». У нас всегда есть то, на что ты рассчитываешь. Просто наблюдай и в нужный момент – покупай.</p>
            
            <p>Хочешь посмотреть, например, все дешевые авиабилеты в выбранный город на стене сообщества?</p>
            
            <p>Используй готовые хэштеги в каждом авиабилете для моментального показа нужных билетов в определенный город или выбранную страну.</p>
            
            <p>Хочешь увидеть в режиме реального времени в одной новостной ленте сразу все дешевые авиабилеты гипермаркета?</p>
            
            <p>Используй хэштег #авиабилеты. Ты тоже найдешь его в каждом дешевом авиабилете.</p>
            
            <p>Хочешь получать все дешевые авиабилеты в ленту новостей? Подпишись на нужное сообщество в разделе "Витрина".</p>
            
            <p>Торопишься, листая ленту новостей? Фотография достопримечательности в городе назначения поможет быстро найти нужный билет на стене сообщества или в своей ленте новостей.</p>
            
            <p>Хочешь в личные сообщения?</p>
            
            <p>Подпишись на выборочные (или все) направления полетов из нужного сообщества в разделе "Витрина". Один раздел - все возможности работы с 700 сообществами гипермаркета "Дешевые авиабилеты".</p>
            
            <p>Подписался на определенное направление и интересуют другие?</p>
            
            <p>В каждом дешевом авиабилете мы показываем WIKI-меню с картой полетов по всему миру. Выбирай.</p>
            
            <p>А еще мы показываем количество пересадок, название авиакомпаний-перевозчиков и дни недели вылета.</p>
            
            <p>Теперь ты знаешь, как все работает. Загляни в своё будущее сегодня и добро пожаловать на борт! Ах да… Не говори соседям в салоне самолета, что купила перелет из Парижа в Мартинику за 8000 рублей или из Москвы в Париж за 2300. Не расстраивай людей…</p>
            </div>
           }
       </Text>
    </Div>
)
export default  How_Work;