import React, { Component } from 'react';
import { Div, Text, Title} from '@vkontakte/vkui';

const Friends_help = ({ sex}) => (
    <Div>
        <Title weight='heavy' level='3'>Навестить друзей</Title>
        <Text>
            {sex == 2 ? 
            <div>
                <p>В списке друзей у тебя 100 человек, на свадьбе — 10, на дне рождения — 3, а когда проблема — всего 1 и скорее всего это будет твоя мама?</p>

                <p>Если это не так – поздравляем и напоминаем, что все во многом зависит от тебя самого, потому, что друзьям нужно уделять и внимание и время.</p>

                <p>Посмотри, сколько стоит авиаперелет к другу или подруге – навести друзей!</p>

                <p>В списке друзей могут быть не все – мы показываем только тех, кто живет в других городах. К некоторым из них нельзя полететь на самолете и, возможно, немного позже, мы предложим их навестить на поезде или автобусе.</p>

                <p>У тебя сотни друзей? Поздравляем! Воспользуйся строкой поиска или сортировкой по имени, фамилии, возрасту, дням рождения или знакам зодиака.</p>

                <p>Не увидел подруг? Они в соседней вкладке сверху – не забудь поздравить с Днюхой. Если у тебя в настройках отключены оповещения о днях рождения друзей – посмотри календарь дней рождения. Социализация – человеческая потребность в общении и да, слетай! Жалеть не будешь.</p>
            </div>:
            <div>
                <p>В списке друзей у тебя 100 человек, на свадьбе — 10, на дне рождения — 3 школьные подруги, а когда проблема — всего 1 и скорее всего это будет твоя мама?</p>

                <p>Если это не так – поздравляем и напоминаем, что все во многом зависит от тебя любимой, потому, что друзьям и подругам нужно уделять и внимание и время.</p>

                <p>Посмотри, сколько стоит авиаперелет к подруге и пришли ссылку (здесь на слово «ссылку» нужна ссылка на поделиться приложением. Если не найдете – уберите весь абзац) на приложение другу (пусть посмотрит стоимость перелета к тебе). Навести подруг!</p>

                <p>В списке друзей могут быть не все – мы показываем только тех, кто живет в других городах. К некоторым из них нельзя полететь на самолете и, возможно, немного позже, мы предложим их навестить на поезде или автобусе.</p>

                <p>У тебя сотни друзей? Поздравляем! Воспользуйся строкой поиска или сортировкой по имени, фамилии, возрасту, дням рождения или знакам зодиака.</p>

                <p>Не увидела подруг? Они в соседней вкладке сверху – не забудь поздравить с Днюхой. Если у тебя в настройках отключены оповещения о днях рождения друзей – посмотри календарь дней рождения. Социализация – человеческая потребность в общении и да, слетай! Жалеть не будешь.</p>
            </div>}
        </Text>
    </Div>
)
export default Friends_help;