                <p>Мы заходим в аптеку и идем напрямую к прилавку, произносим название того, что нам нужно и узнаем о наличии и цене. Если в этой аптеке в этот момент нужного товара нет, то можно оставить заказ. Похоже на покупку авиабилета?</p>

